@import "../../scss/variables.scss";

.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  border-bottom: solid 0.2px rgb(218, 218, 218);

  &:last-child {
    border: none;
  }

  .task__check {
    display: flex;
    align-items: center;
  }

  .task__completed {
    text-decoration: line-through;
    font-style: italic;
    color: gray;
  }

  .task__time {
    display: flex;
    flex-direction: column;
    justify-content: left;
  }

  .checkbox {
    margin-right: 1.2rem;
  }

  .date {
    font-size: 0.7rem;
    color: gray;
  }

  .icon {
    margin-left: 1.2rem;
    color: $color-one;
  }
}
