@import "../../scss/variables.scss";

.form {
  box-shadow: 0 0 3px 0 #ccc;
  border-bottom: 0.1rem;
  padding: 1rem;
  position: relative;
  width: 380px;
  margin: 1rem auto;
  margin-bottom: 2rem;
  transition: all 1s;

  .icon {
    width: 2rem;
    height: 2rem;
    background-color: $color-one;
    border-radius: 50px;
    color: white;
    padding: 0.5rem;
    position: absolute;
    top: 81%;
    left: 85%;
    text-align: center;
  }
}

@media screen and (max-width: 500px) {
  .form {
    width: 90vw;
    margin: 1.5rem auto;
    margin-bottom: 1.5rem;
  }
}
