.list {
  width: 380px;
  margin: 1rem auto;
  box-shadow: 0 0 3px 0 #ccc;
  border-bottom: 0.1rem;
  height: 380px;
  overflow: auto;
  transition: all 1s;

  .empty__msg {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 8rem;
  }

  .cloud__icon {
    width: 3rem;
    height: 3rem;
    margin: auto;
  }

  span {
    color: gray;
    font-style: italic;
    font-size: 0.85rem;
    text-align: center;
    padding: 0.4rem 0;
  }
}

@media screen and (max-width: 500px) {
  .list {
    width: 90vw;
    margin: auto;
    margin-bottom: 1.5rem;
  }
}
